@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap");
.topbar-wrapper {
  width: 100%;
  background-color: white;
  height: 70px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0px 1px 10px 1px rgba(0, 0, 0, 0.4);
  font-family: "Poppins", sans-serif;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 2;
}
.topbar-left {
  margin-left: 25px;
}
.topbar-left h1 {
  color: #11998e;
}
.topbar-right {
  margin-right: 25px;
}
.topbar-right button {
  font-size: 18px;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0.1);
  border-style: none;
  padding: 8px 16px;
  border-radius: 4px;
}
.topbar-right button:hover {
  background-color: rgba(0, 0, 0, 0.5);
  color: white;
}
.menu {
  text-decoration: none;
  font-size: 20px;
  font-style: bold;
  color: black;
  margin: 0px 8px;
  border-radius: 4px;
  padding: 4px 8px;
  transition: all 0.3s;
}
.menu:hover {
  background-color: rgba(0, 0, 0, 0.5);
  color: white;
}
.active {
  color: green;
}
