@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap");
.form-wrapper {
  width: 100%;
  height: 100vh;
  background: #7f7fd5;
  background: -webkit-linear-gradient(to right, #91eae4, #86a8e7, #7f7fd5);
  background: linear-gradient(to right, #91eae4, #86a8e7, #7f7fd5);
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: "Poppins", sans-serif;
  font-weight: 400;
}
.inner-form-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 50px 50px 10px;
  background-color: white;
  border-radius: 10px;
}
.inner-form-wrapper input {
  width: 100%;
  margin-bottom: 18px;
  height: 30px;
  padding: 4px;
  font-size: 14px;
  background-color: white;
  border: 1px solid black;
  outline: none;
  border-radius: 6px;
}
#submit-btn {
  width: 110px;
  font-size: 16px;
  cursor: pointer;
  height: 30px;
  border-radius: 6px;
  background-color: rgba(84, 107, 184, 0.5);
  margin-top: 25px;
  border: none;
}
#submit-btn:hover {
  background-color: #7f7fd5;
  color: white;
}
