@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap);
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.form-wrapper {
  width: 100%;
  height: 100vh;
  background: #7f7fd5;
  background: linear-gradient(to right, #91eae4, #86a8e7, #7f7fd5);
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: "Poppins", sans-serif;
  font-weight: 400;
}
.inner-form-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 50px 50px 10px;
  background-color: white;
  border-radius: 10px;
}
.inner-form-wrapper input {
  width: 100%;
  margin-bottom: 18px;
  height: 30px;
  padding: 4px;
  font-size: 14px;
  background-color: white;
  border: 1px solid black;
  outline: none;
  border-radius: 6px;
}
#submit-btn {
  width: 110px;
  font-size: 16px;
  cursor: pointer;
  height: 30px;
  border-radius: 6px;
  background-color: rgba(84, 107, 184, 0.5);
  margin-top: 25px;
  border: none;
}
#submit-btn:hover {
  background-color: #7f7fd5;
  color: white;
}

.topbar-wrapper {
  width: 100%;
  background-color: white;
  height: 70px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0px 1px 10px 1px rgba(0, 0, 0, 0.4);
  font-family: "Poppins", sans-serif;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 2;
}
.topbar-left {
  margin-left: 25px;
}
.topbar-left h1 {
  color: #11998e;
}
.topbar-right {
  margin-right: 25px;
}
.topbar-right button {
  font-size: 18px;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0.1);
  border-style: none;
  padding: 8px 16px;
  border-radius: 4px;
}
.topbar-right button:hover {
  background-color: rgba(0, 0, 0, 0.5);
  color: white;
}
.menu {
  text-decoration: none;
  font-size: 20px;
  font-style: bold;
  color: black;
  margin: 0px 8px;
  border-radius: 4px;
  padding: 4px 8px;
  transition: all 0.3s;
}
.menu:hover {
  background-color: rgba(0, 0, 0, 0.5);
  color: white;
}
.active {
  color: green;
}

.outer-wrapper {
  width: 100%;
  margin-top: 80px;
  display: flex;
  flex-direction: column;
  font-family: "Poppins", sans-serif;
}
.main-heading {
  align-self: flex-start;
}
.inner-wrapper {
  width: 100%;
  align-self: flex-end;
}
.product-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0px 50px;
}
.medicineForm {
  width: 400px;
  padding-top: 50px;
  height: 400px;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  background-color: rgba(0, 0, 0, 0.8);
  border-radius: 6px;
  display: flex;
  flex-direction: column;
}
.medicineForm input {
  font-size: 18px;
  padding: 8px;
  margin: 8px;
  width: 80%;
  border-radius: 4px;
}
#sub {
  width: 150px;
}

